.FormStyles {
  width: 100%;
  text-align: left;
}

.form-group {
  width: 100%;
  margin-bottom: 2rem;
}

.FormStyles .label {
  font-size: 1.8rem;
}

.FormStyles input,
.FormStyles textarea {
  width: 100%;
  font-size: 2rem;
  padding: 1.2rem;
  color: var(--gray-1);
  background-color: var(--deep-dark);
  outline: none;
  border: none;
  border-radius: 8px;
  margin-top: 1rem;
}

textarea {
  min-height: 250px;
  resize: vertical;
}

.FormStyles button[type="submit"] {
  background-color: var(--gray-1);
  color: var(--black);
  font-size: 2rem;
  display: inline-block;
  outline: none;
  border: none;
  padding: 1rem 4rem;
  border-radius: 8px;
  cursor: pointer;
}
