.ItemStyles {
  padding: 2rem;
  background-color: var(--deep-dark);
  display: flex;
  align-items: center;
  gap: 2rem;
  border-radius: 8px;
  margin-bottom: 2rem;
}

.ItemStyles .icon {
  color: var(--white);
  background-color: var(--gray-2);
  padding: 1.3rem;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.icon svg {
  width: 3.5rem;
}
